<template>
  <div>
    <b-modal :id="infoModalperfil.id" title="EDITAR PERFIL" ok-only  hide-footer>
        <perfil></perfil>
      </b-modal>
      <b-table bordered
        show-empty
        small    
        head-variant="light"      
        :items="items"
        :fields="fields">
        <template v-slot:cell(editar)="row">
        <b-button size="sm" @click="infoa(row.item, row.index, $event.target)" class="mr-1">
          Editar
        </b-button>       
      </template>          
        </b-table>         
    </div>
</template>
<script>
import perfil from '../03-Perfiles/modalPerfileditar.vue'
export default {
   components:{
     perfil
   },
    data() {
      return {
        items: [
          { ID: 12341, nombre: 'PERFIL 1' },
          { ID: 12342, nombre: 'PERFIL 2' },
          { ID: 12343, nombre: 'PERFIL 3' },
          { ID: 12344, nombre: 'PERFIL 4' }
        ],
        fields: [
            { key: 'ID', label: 'ID ' },
            { key: 'nombre', label: 'Nombre de Perfil' },
            { key: 'editar', label: 'Editar' }            
         ],         
         infoModalperfil: {
          id: 'info-modal4',
          title: '',
          content: ''
        }       
      }
    },    
     methods: {
     infoa(item, index, button) {
        this.infoModalperfil.title = `Row index: ${index}`
        this.infoModalperfil.content = JSON.stringify(item, null, 2)
        this.$root.$emit('bv::show::modal', this.infoModalperfil.id, button)
      }
  }
}
</script>