<template>
    <div>
    <b-form @submit="onSubmit" v-if="show">      
      <b-table fixed small :items="items" thead-class="hidden_header"></b-table>        
      <b-form-group id="input-group-3" label="Area:" label-for="input-3">
        <b-form-select
          id="input-3"
          v-model="form.area"
          :options="area"
          required
        ></b-form-select>
      </b-form-group> 
      <b-form-group id="input-group-4" label="Perfil:" label-for="input-4">
        <b-form-select
          id="input-4"
          v-model="form.perfil"
          :options="perfil"
          required
        ></b-form-select>
      </b-form-group> 

      <b-form-checkbox
      id="checkbox-1"
      v-model="checaEstatus"
      name="checkbox-1"
      value="accepted"
      unchecked-value="not_accepted"
      
    >
      Activo
    </b-form-checkbox>
      <p></p>
      <b-container class="bv-example-row">
        <b-row>
            <b-col cols="6"></b-col>
            <b-col cols="2">
                <b-button type="submit" variant="success">Actualizar</b-button>                
            </b-col>
            <b-col cols="1">
            </b-col>
            <b-col cols="2">
                <b-button @click="$bvModal.hide('modal-0')">Cancelar</b-button>
            </b-col>
        </b-row>        
      </b-container>
    </b-form>   
    </div>
</template>
<script>
export default {
    data() {
      return {
        form: {
           area: null,
           perfil: null
        }, 
        area: [{ text: 'Seleccionar Área', value: null }, 'Area 1', 'Area 2', 'Area 3', 'Area 4'],
        perfil: [{ text: 'Seleccionar Perfil', value: null }, 'Perfil 1', 'Perfil 2', 'Perfil 3', 'Perfil 4'],       
        show: true,
        checaEstatus:[],
        items: [
          { campo: 'ID', valor: '12345' },
          { campo: 'Nombre', valor: 'Larsen' },
          { campo: 'Apellido Paterno', valor: 'Geneva' },
          { campo: 'Apellido Materno', valor: 'Jami' },
          { campo: 'Correo Electrónico', valor: 'correo@correo.com' }
        ]
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
       
        this.showConfirm()
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
          
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.$bvModal.hide('modal-0')
          })
          .catch(err => {
            // An error occurred
          })
      }
    }
  }
</script>
<style>
.hidden_header {
  display: none;
}
</style>