<template>
<div>
<b-form @submit="onSubmit" v-if="show">
      <b-form-group
        id="input-group-1"
        label="Área"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="form.area"          
          required
          placeholder="Introduce un área"
        ></b-form-input>
      </b-form-group>
      <b-form-group id="input-group-2" label="Descripción:" label-for="input-2">
        <b-form-textarea
        id="input-2"
        v-model="form.descripcion"
        placeholder="Introduce una descripción"
        rows="3"
        max-rows="6"
    ></b-form-textarea>
      </b-form-group>
<b-form-checkbox
      id="checkbox-1"
      v-model="form.checaEstatus"
      name="checkbox-1"
      value="accepted"
      unchecked-value="not_accepted"
    >
      Activo
    </b-form-checkbox>
      <p></p>
      <b-container class="bv-example-row">
        <b-row>
            <b-col cols="6"></b-col>
            <b-col cols="2">
                <b-button type="submit" variant="success">Guardar</b-button>                
            </b-col>
            <b-col cols="1">
            </b-col>
            <b-col cols="2">
                <b-button @click="$bvModal.hide('modal-2')">Cancelar</b-button>
            </b-col>
        </b-row>        
      </b-container>
    </b-form>
   
   
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          area: '',
          descripcion:'',
          checaEstatus: []
        },
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
        this.showConfirm()
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.area = ''
        this.form.checaEstatus = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.$bvModal.hide('modal-2')
          })
          .catch(err => {
            // An error occurred
          })
      }
    }
  }
</script>