<template>
<div>
<b-form @submit="onSubmit" v-if="show">
      <b-form-group
        id="input-group-1"
        label="Perfil"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="form.perfil"          
          required
          placeholder="Introduce un Perfil"
        ></b-form-input>
      </b-form-group>
     

      <p></p>
      <b-container class="bv-example-row">
        <b-row>
            <b-col cols="6"></b-col>
            <b-col cols="2">
                <b-button type="submit" variant="success">Guardar</b-button>                
            </b-col>
            <b-col cols="1">
            </b-col>
            <b-col cols="2">
                <b-button @click="$bvModal.hide('modal-perfil-alta')">Cancelar</b-button>
            </b-col>
        </b-row>        
      </b-container>
    </b-form>
   
   
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          perfil: ''
        },
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
        this.$bvModal.hide('modal-perfil-alta')
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.perfil = ''
       
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>